.Spinner_Loader__31Olc {
    padding: 0;
    border: 7px solid #f3f3f3;
    border-radius: 50%;
    border-top: 7px solid #3498db;
    width: 40px;
    height: 40px;
    -webkit-animation: Spinner_spin__36VGo 2s linear infinite;
    animation: Spinner_spin__36VGo 2s linear infinite;
  }
  
  @-webkit-keyframes Spinner_spin__36VGo {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes Spinner_spin__36VGo {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
